    // VALUES -------------------------------------------------------------------------------------------------------------
    #values {
        padding: 1rem 12% 5%;
        background-color: #f8fbfd;
        position: relative;
        .dark_background {
            background: url("assets/img/values_background.svg");
            background-repeat: no-repeat;
            position: absolute;
            background-size: cover;
            width: 100%;
            height: 60%;
            bottom: 0;
            z-index: 1;
            left: 0;
            right: 0;
        }
        .card {
            position: relative;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0);
            padding: 5rem;
            text-align: left;
            background-image: url("assets/img/val_card_background.svg");
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            border: none;
            border-radius: 10px;
            -webkit-border-radius: 10px;
            -moz-border-radius: 10px;
